body {
  margin: 0;
  font-family: sans-serif;
}

.people {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.person {
  text-align: center;
  border-radius: 12px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 20px;
  transition: all 1s;
  display: flex;
  position: relative;
  overflow: visible;
}

.person .photo {
  border-radius: 50%;
  width: 200px;
  height: 200px;
  margin-bottom: 15px;
  transition: all 1s;
  position: relative;
  overflow: hidden;
}

.person .photo img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

.person .photo .rank {
  color: #8b0000;
  background-color: #fff;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  font-size: 16px;
  font-weight: 700;
  display: flex;
  position: absolute;
  top: 50%;
  right: 5px;
  transform: translateY(-50%);
}

.person .details .name {
  color: #333;
  font-size: 20px;
  font-weight: 700;
  text-decoration: none;
}

.person .details .name:hover {
  color: #007bff;
}

.person .details .extra {
  color: #777;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: 5px;
  font-size: 18px;
  display: none;
}

.person .details .extra .sport {
  margin: 0;
}

.person .details .extra .pay {
  white-space: nowrap;
}

.person .details .extra .pay:before {
  content: "$";
}

.person .details .extra .pay:after {
  content: "M";
}

.person:hover {
  z-index: 100;
  background-color: #eee;
  transform: scale(1);
}

.person:hover .photo {
  border-radius: 60px;
  width: 240px;
  height: 240px;
}

.person:hover .details .extra {
  display: flex;
}

@media (width >= 500px) {
  .people {
    grid-template-columns: repeat(2, 1fr);
    max-width: 450px;
    margin: 0 auto;
    display: grid;
  }
}

@media (width >= 750px) {
  .people {
    max-width: 500px;
  }
}

@media (width >= 1000px) {
  .people {
    grid-template-columns: repeat(4, 1fr);
    max-width: 900px;
  }
}

@media (width >= 1500px) {
  .people {
    max-width: 1000px;
  }
}
